<template>
  <div class="blog">
    <hero-view></hero-view>

    <section class="section">
      <div class="container">
        <h1 class="title">算法</h1>
        <h2 class="subtitle is-4">算法狂想曲</h2>
        <div class="columns is-multiline" v-if="als && als.length">
          <router-link
            tag="a"
            class="column is-one-third"
            v-for="al of als"
            v-bind:key="al._id"
            :to="`/blogposts/${al._id}`"
          >
            <div class="card">
              <div class="card-content">
                <p class="title">{{ al.title }}</p>
                <p class="answer">{{ al.createdAt|timer }}</p>
              </div>
            </div>
          </router-link>
        </div>
      </div>
    </section>

    <section class="section">
      <div class="container">
        <h1 class="title">数据结构</h1>
        <h2 class="subtitle is-4">结构之美</h2>
        <div class="columns is-multiline" v-if="datas && datas.length">
          <router-link
            tag="a"
            class="column is-one-third"
            v-for="data of datas"
            v-bind:key="data._id"
            :to="`/blogposts/${data._id}`"
          >
            <div class="card">
              <div class="card-content">
                <p class="title">{{ data.title }}</p>
                <p class="answer">{{ data.createdAt|timer }}</p>
              </div>
            </div>
          </router-link>
        </div>
      </div>
    </section>

    <section class="section">
      <div class="container">
        <div v-if="thinks && thinks.length">
          <h1 class="title">Think</h1>
          <h2 class="subtitle is-4">我思故我在</h2>
        </div>
        <div class="columns is-multiline" v-if="thinks && thinks.length">
          <router-link
            tag="a"
            class="column is-one-third"
            v-for="think of thinks"
            v-bind:key="think._id"
            :to="`/blogposts/${think._id}`"
          >
            <div class="card">
              <div class="card-content">
                <p class="title">{{ think.title }}</p>
                <p class="answer">{{ think.createdAt|timer }}</p>
              </div>
            </div>
          </router-link>
        </div>
      </div>
    </section>

    <router-view></router-view>
  </div>
</template>

<script>
import HeroView from "./common/HeroView";
export default {
  name: "blog",
  components: {
    HeroView
  },
  data: () => ({
    als: [],
    datas: [],
    thinks: []
  }),
  methods: {
    async fetch() {
      const res = await this.$http.get("blog");
      res.data.forEach(item => {
        if (item.category === "Algorithm") {
          this.als.push(item);
        } else if (item.category === "DataStructrue") {
          this.datas.push(item);
        } else {
          this.thinks.push(item);
        }
      });
    }
  },
  created() {
    this.fetch();
  },
  filters: {
    timer: value => {
      return String(value).split("T")[0];
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
@import '../mq'

.hero
  background: url('../assets/clouds.jpg')
  background-size: cover
  .title
    +mobile
      font-weight: bold
    +tablet
      font-size: 2.5rem
    +desktop
      font-size: 4rem
      margin-top: 2rem

.section
  width: 80%
  margin: auto

h2
  margin: 1.5rem 0 2rem 0 !important

.columns is-multiline
  background-color: Light 
  flex-wrap: wrap
  
.answer
  margin-top: 10px !important
  color: gray

.card
    margin: 1rem

p:active
  color: $primary

</style>
