<template>
  <div class="home">
    <hero-view></hero-view>
    <div class="section">
      <section class="container">
        <div class="columns is-multiline" v-if="faqs && faqs.length">
          <div class="column is-half" v-for="faq of faqs" :key="faq.index">
            <div class="card">
              <header class="card-header">
                <span class="tag is-info">Info</span>
              </header>
              <div class="card-content">
                <p class="title">{{ faq.question }}</p>
                <p class="answer">{{ faq.anwser }}</p>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
import HeroView from "./common/HeroView";

export default {
  name: "home",
  components: {
    HeroView
  },
  data: () => {
    return {
      faqs: []
    };
  },

  methods: {
    async fetch() {
      const res = await this.$http.get("home");
      this.faqs = res.data;
    }
  },
  created() {
    this.fetch();
  }
};
</script>

