<template>
  <div class="blogpost">
    <div class="content">
      <article class="message">
        <div class="message-body">
          <div v-html="model.body"></div>
        </div>
      </article>
    </div>
  </div>
</template>

<script>
export default {
  name: "blogpost",
  data() {
    return {
      model: {}
    };
  },
  watch: {
    title: "fetch"
    // id(){
    //   this.fetch()
    // }
  },
  methods: {
    async fetch() {
      const res = await this.$http.get(this.$route.path);
      this.model = res.data;
    }
  },
  created() {
    this.fetch();
  }
};
</script>

<style lang="sass" scoped>
.content
  width: 75%
  margin: 0 auto
  margin-top:5rem
  margin-bottom: 5rem
</style>