import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Blog from '@/components/Blog'
import Faq from '@/components/Faq'
import BlogPost from '@/components/BlogPost'


Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/blog',
      name: 'Blog',
      component: Blog,
    },
    {
      path: '/faq',
      name: 'Faq',
      component: Faq
    },
    {
      path: '/blogposts/:id*',
      name: 'BlogPost',
      component: BlogPost
    },
  ],
  mode: 'hash'
})